<template>
  <div id="Menu" class="grid" @click="hide(this.event)">
    <ul class="menutype">
      <li v-for="keys in menuType" :data-parentId="keys.parentId" @click="show(this.event)">
        <p v-text="keys.name"></p>
        <ul>
          <li v-for="key in keys.list" v-text="key.name" :data-id="key.id" :data-parentId="key.parentId"
              @click.stop="getMenuList(this.event)"></li>
        </ul>
      </li>
    </ul>
    <ul class="menuList col-12">
      <li v-for="key in menuList" :data-id="key.id" @click.stop="getMenuDetail($event)">
        <img :src="key.albums[0]"  />
        <div>
          <h3 v-text="key.title" ></h3>
          <p v-text="key.tags" ></p>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
  import './../../static/js/flexible.js';
  import './../../static/js/makegrid.js';
  import menulist from './../../static/js/menulist.json';

  lib.flexible.makeGrid({
    designWidth: 640, //设计稿宽度
    designUnit: 1, //最小px值
    columnCount: 6, //列数
    columnXUnit: 24, //列宽
    gutterXUnit: 0.1, //列间距
    edgeXUnit: 1 //边距
  });
  import axios from 'axios';
  import jsonp from 'jsonp';

  export default {
    name: 'Menu',
    data: function () {
      return {
        menu: '猪肉',
        page: 1,
        menuList: menulist,
        menuType: [],
        menuDetails: []
      }
    },
    created: function () {
      const self = this;
      let url = 'http://apis.juhe.cn/cook/query?key=4d3a1813fafadfa830f57ca1ee123727&menu=' + this.menu + '&pn=' + this.page + '&rn=20';
      let url_type = 'http://apis.juhe.cn/cook/category.php?key=4d3a1813fafadfa830f57ca1ee123727';
      /*jsonp(url,null,function(err,res){
       if(res.resultcode === '200'){
       //请求成功
       self.menuList.push(res.result.data);
       }
       });*/
      /*axios.get('/menu',{params:{"city": '静安区'}}).then(function(res){
       let record = res.data.results[0];
       self.menuList.push(record.index);
       })*/
      /*axios.get('/menutype').then((res)=>{
       if( res.resultcode === "200" ){
       self.menuType.push(res.result.data)
       }
       console.log(res);
       })*/
      jsonp(url_type, null, (err, res) => {
        if (res.resultcode === "200") {
          let i = 0,
            len = res.result.length;
          for (i = 0; i < len; i++) {
            self.menuType.push(res.result[i])
          }
        }
      })
    },
    methods: {
      show(e) {
        e.currentTarget.lastChild.style.display = 'block';
        let child = e.target.nodeName.toLowerCase() === 'li' ? siblings(e.currentTarget.parentNode.parentNode) : siblings(e.currentTarget.parentNode);

        function siblings(elem) {
          var a = [];
          var b = elem.parentNode.children;
          for (let i = 0; i < b.length; i++) {
            if (b[i] !== elem) a.push(b[i]);
          }
          return a;
        };

        for (let j = 0; j < child.length; j++) {
          child[j].lastChild.style.display = 'none';
        }
        ;
        e.stopPropagation();
      },
      hide(e){
        let elem, i = 0,len = 0;
        if (e.target.className !== 'menutype') {
          elem = document.getElementsByClassName('menutype')[0].getElementsByTagName('ul');
          len = elem.length;
          if(len <= 0) return;
          for (; i < elem.length; i++) {
            elem[i].style.display = 'none';
          }
        }
      },
      getMenuList(e){
        let name = e.currentTarget.innerText;
        this.$router.push({path: 'list', query: {plan: name}});
      },
      getMenuDetail(e){
          let ID = e.currentTarget.getAttribute('data-id');
          this.$router.push({path: 'details', query: {plan: ID}});
      }
    },
  }
</script>
<style lang="scss" type="text/css">
  #Menu {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }

  .menuList li {
    position: relative;
    margin: 0;
    float: none;
    border-bottom: .5rem solid #f0f0f0;
  }

  .menuList li:last-child {
    border-bottom: none;
  }

  .menuList img {
    width: 10rem;
    vertical-align: bottom;
  }

  .menuList div {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 1rem 0;
    background-color: rgba(255, 255, 255, .6);
  }

  .menuList h3 {
    margin-bottom: .5rem;
  }

  .menutype {
    width: 2rem;
    position: fixed;
    top: 1rem;
    left: 0;
    z-index: 1;
    background-color: rgba(255, 255, 255, .8);

    li {
      position: relative;
      border-bottom: 1px dashed #CCCCCC;
      line-height: .5rem;
    }

    li:nth-last-child(1) {
      border-bottom: none;
    }

    ul {
      display: none;
      position: absolute;
      left: 2rem;
      top: 0;

      li {
        width: 2rem;
      }

    }

  }
</style>



