<template>
  <div class="list">
    <ul class="menuList col-12">
      <li v-for="key in list" :data-id="key.id" @click="getMenuDetail(this.event)">
        <img :src="key.albums[0]" />
        <div>
          <h3 v-text="key.title"></h3>
          <p v-text="key.tags"></p>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
  import './../../static/js/flexible.js';
  import './../../static/js/makegrid.js';
  import jsonp from 'jsonp';
  export default {
    name: 'list',
    data () {
      return {
        list: []
      }
    },
    created: function () {
      const self = this;
      let name = decodeURI(location.search.substring(location.search.indexOf('=')+1)),
          url = 'http://apis.juhe.cn/cook/query.php?key=4d3a1813fafadfa830f57ca1ee123727&menu='+name+'&pn=1&rn=10',
          i = 0,
          len = 0,
          data =[];

      jsonp(url,null,(err,res)=>{
        if(res.resultcode === "200"){
          this.$router.push({path: 'list',query:{plan:name}});
          len = res.result.data.length;
          data = res.result.data;
          for(;i < len; i++){
            self.list.push(data[i])
          }
        }
      })
    },
    methods:{
      getMenuDetail(e){
        let ID = e.currentTarget.getAttribute('data-id');
        this.$router.push({path: 'details', query: {plan: ID}});
      }
    }
  }
</script>
<style lang="scss" type="text/css">
  .list{
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }
  .menuList li {
    position: relative;
    margin: 0;
    float: none;
    border-bottom: .5rem solid #f0f0f0;
  }

  .menuList li:last-child {
    border-bottom: none;
  }

  .menuList img {
    width: 10rem;
    vertical-align: bottom;
  }

  .menuList div {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 1rem 0;
    background-color: rgba(255, 255, 255, .6);
  }

  .menuList h3 {
    margin-bottom: .5rem;
  }
</style>



