<template>
  <div class="details">
      <div class="col-12" v-for="key in detail" :data-id="key.id">
        <div class="describe">
          <img :src="key.albums[0]"/>
          <div>
            <h3 v-text="key.title"></h3>
            <p v-text="key.tags"></p>
          </div>
        </div>
        <p><strong>主菜：</strong><span v-text="key.ingredients"></span></p>
        <p><strong>配菜：</strong><span v-text="key.burden"></span></p>
        <p><strong>提示：</strong><span v-text="key.imtro"></span></span></p>
        <div v-for="(res,index) in key.steps">
          <img :src="res.img" />
          <p v-text="res.step"></p>
        </div>
      </div>
  </div>
</template>
<script>
  import './../../static/js/flexible.js';
  import './../../static/js/makegrid.js';
  import jsonp from 'jsonp';
  export default{
    name: 'details',
    data(){
      return {
        detail: []
      }
    },
    created: function () {
      const self = this;
      let Id_id = decodeURI(location.search.substring(location.search.indexOf('=') + 1)),
          url = 'http://apis.juhe.cn/cook/queryid?key=4d3a1813fafadfa830f57ca1ee123727&id='+ Id_id,
          i = 0,
          len = 0,
          data = [];

      jsonp(url, null, (err, res) => {
        if (res.resultcode === "200") {
          len = res.result.data.length;
          data = res.result.data;
          for (; i < len; i++) {
            self.detail.push(data[i])
          }
        }
      });
    }
  }
</script>
<style type="text/css" lang="scss">
  .details{
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
    img{
      width: 10rem;
    }
    p{
      margin: .3rem 0;
      text-align: left;
      text-indent: 2em;
    }
     .describe{
       position: relative;
       p{
         text-align: center;
       }
       div{
         width: 100%;
         position: absolute;
         bottom: 0;
         left: 0;
         padding: 1rem 0;
         background-color: rgba(255, 255, 255, .6);
       }
     }
    h3 {
      margin-bottom: .5rem;
    }
  }
</style>



