		"imtro": "今天难得休息、就到横滨以前研究院的同学家作客。她因有事出门、所以我也閒著..先帮她把房间大清扫了一番后、顺便煮了两道家常菜、準备等她回来一起吃。 因她是单身、又不会煮、所以厨房基本是没有多餘的辅佐材料、只有上个月前我来时留下的八角、一根山药、冷冻五花肉、真晕！ 在日本山药基本都是生吃的、磨碎后浇饭（或麵条）一起吃。但今天我把它拿来用炒的、虽然没蒜头等辅佐料、还是有模有样的煮了一道中华菜餚、很香、很好吃哦！",
