		"imtro": "如果你是嗜肉族，早已厌倦了鸡蛋，如果你喜欢在外面吃烤串，看了那么多猫鼠做肉串的新闻你会不会心惊肉跳？如果拥有一台可以在家做放心肉卷的机器，你还会去外面吃吗？真材实料的肉卷绝对比肉串吃得过瘾，只需一台小小的立式煎蛋器，一滴油，无论你想吃蛋卷还是肉卷还是蛋裹肉卷随你便，顷刻间飘来的肉香绝对让你垂涎三尺，刷牙的功夫一根肉卷就做好了，一根不过瘾就多来几根吧，如果心情好做个造型也还不错哦，看看我家盘子里就养了5头可爱的香味四溢的小猪，猪肉加上奶酪，两样都是辰辰的最爱啊。",
