		"title": "自制猪肉丸",
		"tags": "家常菜;热菜;快手菜",
		"imtro": "肉丸原本是宁德蕉城人民在过年时候必吃的一种食物，地位相当于北方的饺子。后来由于改革开放的春风，就像饺子馆遍地都是一样，肉丸摊子也是大街小巷处处可见。随着外出务工和外出学习等人口流动量的逐年加大，由于宁德蕉城没有什么其它的特色小吃，因此肉丸成了人们思念家乡的一种方式。因为这种肉丸子只有在宁德蕉城才有，而且摊子数量还在逐年减少，因此人们特别怀念它们。不管是男女老少都比较喜欢。",
		"ingredients": "猪肉,250g",
		"burden": "盐,适量;生粉,少许;生姜,少许",
		"albums": [
			"http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/t/2/1867_615071.jpg"
		],
		"steps": [
			{
				"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/19/1867_18fb1748caa090b7.jpg",
				"step": "1.准备猪肉一块，肥瘦参半即可"
			},
			{
				"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/19/1867_c8d1ec51eadb2cfb.jpg",
				"step": "2.把肉切丁，加入姜末"
			},
			{
				"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/19/1867_d86f2c9901f24e14.jpg",
				"step": "3.放入搅拌机内搅打成肉泥，期间放入少量的水一起搅拌"
			},
			{
				"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/19/1867_f02243153632ad34.jpg",
				"step": "4.取一个大盆把肉泥装出来，然后放入盐，生粉朝一个方向搅打上劲"
			},
			{
				"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/19/1867_6a9eb210c34f149d.jpg",
				"step": "5.然后用手攥出一个乒乓球大小的肉丸来"
			},
			{
				"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/19/1867_05128b303b50a671.jpg",
				"step": "6.用个小勺接一下"
			},
			{
				"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/19/1867_d812190dad56089b.jpg",
				"step": "7.然后把勺子里的肉丸放入锅内，此时锅内的水温不宜过高，水不要翻滚就行"
			},
			{
				"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/19/1867_e467455b2ed02841.jpg",
				"step": "8.然后逐个把肉丸挤进锅内，如肉丸都上浮到水面时，即可开大火烧滚就可以了。"
			}
		]
