		"title": "菠菜汁香菇猪肉饺子",
		"tags": "补血;蒸;孕妇;秋季;饺子;面食;补铁;饺子馅;备孕期",
		"imtro": "菠菜的营养丰富，是很好的家常菜之一，但是菠菜吃的时候要焯水，因为菠菜含有草酸，圆叶品种含量尤多，吃后影响人体对钙的吸收，因此，吃此品种菠菜时宜先焯水去掉菜水，以减少草酸含量。 如果准备做妈妈，那就得更要吃菠菜了，因为里面含有叶酸，它可以很好的保护准妈妈们生个健康的宝贝，叶酸最重要的功能就是制造红血球和白血球，增强免疫能力，一旦缺乏叶酸，会发生严重贫血，因此叶酸又被称为“造血维生素”。 叶酸是预防宝宝出生缺陷的一种重要方式，准备做准妈妈都需要叶酸（包括天然叶酸和叶酸补充剂）。孕妇对叶酸的需求量比正常人高4倍。 叶酸具有以下五种作用：　 一：有助于预防神经管缺陷（也叫做“神经管畸形”），包括脊柱裂和无脑儿等非常严重的出生缺陷。而神经管缺陷是中国常见的新生儿先天畸形。 二：可能还有助于降低宝宝其他类型的出生缺陷的风险，比如唇颚裂和某些类型的心脏缺陷。 三：预防孕妇贫血，孕妇的身体需要叶酸来制造正常的红细胞。 四：对于DNA和细胞的基本结构都非常重要。因此，获得充足的叶酸，对快速增长的胎盘和和宝宝都特别重要。 五：服用含叶酸的多种维生素，可能会减少孕妇患先兆子痫的风险。 这次就换着花样吃，用菠菜汁包香菇猪肉饺子吃，这样颜色好看了，味道也不一样了，吃起来也特别的有蔬菜的味道，即补充了叶酸也给准备做妈妈们补充了营养。 菠菜性甘凉，能养血、敛阴、润燥，因而可防治便秘，使人容光焕发。菠菜还富含酶，能刺激肠胃、胰腺的分泌，既助消化，又润肠道，有利于大便顺利排出体外，避免毒素进入血液循环而影响面容，使全身皮肤显得红润、光泽。菠菜还可以补充叶酸和铁质",
